const portraitQueryList = window.matchMedia('(orientation:portrait)');
const phoneQueryList = window.matchMedia('(max-width:480px), (max-height:480px)');

const portraitQuery = {
    portraitMode: () => portraitQueryList.matches,
    addListener: callback => portraitQueryList.addListener(callback),
    removeListener: callback => portraitQueryList.removeListener(callback)
};

const phoneQuery = () => phoneQueryList.matches;

export default {

    data() {
        return {
            portraitQueryMode: portraitQuery.portraitMode(),
            phoneQueryMode: phoneQuery()
        }
    },

    computed: {
        mediaMatch_phone() {
            return this.phoneQueryMode;
        },

        mediaMatch_portrait() {
            return this.portraitQueryMode;
        },

        mediaMatch_xs() {
            return this.$vuetify.breakpoint.name === 'xs';
        },

        mediaMatch_sm() {
            return this.$vuetify.breakpoint.name === 'sm';
        },

        mediaMatch_md() {
            return this.$vuetify.breakpoint.name === 'md';
        },

        mediaMatch_lg() {
            return this.$vuetify.breakpoint.name === 'lg';
        },

        mediaMatch_xl() {
            return this.$vuetify.breakpoint.name === 'xl';
        }
    },

    mounted() {
        portraitQuery.addListener(this._updateOrientation) ;
    },

    beforeDestroy() {
        portraitQuery.removeListener(this._updateOrientation) ;
    },

    methods: {

        /**
         * Bind separate breakpoints for portrait / landscape mode
         */

        orient_xs(portrait, landscape) {
            return this._bindBreakpoint('xs', portrait, landscape);
        },

        orient_sm(portrait, landscape) {
            return this._bindBreakpoint('sm', portrait, landscape);
        },

        orient_md(portrait, landscape) {
            return this._bindBreakpoint('md', portrait, landscape);
        },

        orient_lg(portrait, landscape) {
            return this._bindBreakpoint('lg', portrait, landscape);
        },

        orient_xl(portrait, landscape) {
            return this._bindBreakpoint('xl', portrait, landscape);
        },

        _bindBreakpoint(prefix, portrait, landscape) {
            const binding = {};

            binding[prefix + (this.mediaMatch_portrait ? + portrait : landscape)] = true;

            return binding;
        },

        _updateOrientation() {
            this.portraitQueryMode = portraitQuery.portraitMode();
        }
    }
};

