import Vue from 'vue'
import 'vuetify/dist/vuetify.min.css'
import App from './App.vue'
import vuetify from './plugins/vuetify';


Vue.config.productionTip = false

// Uncatched Exceptions
Vue.config.errorHandler = err => {
  console.log('Exception: ', err)
};

new Vue({
  vuetify,
  render: h => h(App)
}).$mount('#app')
